body{font-family:"cabin";font-size:20px;color:#37371f}body button{color:#0b3c49;background-color:#44a1a0;border-radius:6px;padding:6.66667px 40px}body button:hover{box-shadow:inset 0px 100px 0px rgba(255,255,255,0.2)}body button:active{transition:none;box-shadow:inset 0px 100px 0px rgba(0,0,0,0.1);transform:scale(0.99) translateY(-1px)}body button:focus{outline:none}body a{color:#bdbf09}body a:hover{color:#bdbf09}body input{display:block;margin:auto;color:#37371f;border-radius:6px;border:1px solid silver;padding:6.66667px;width:100%}body input:last-of-type{margin-top:20px}body input:focus{outline:none;box-shadow:0px 5px 10px rgba(0,0,0,0.5)}body select{width:100%;padding:10px 0;border-radius:6px;border:1px solid silver}body select:hover{cursor:pointer}body h1{letter-spacing:5px}body h2{letter-spacing:4px}body h3{letter-spacing:3px}@font-face{font-family:'creteround';src:url("/fonts/CreteRound-Regular.ttf");font-weight:normal;font-style:normal;font-display:swap}@font-face{font-family:'cabin';src:url("/fonts/Cabin-Regular.ttf");font-weight:normal;font-style:normal;font-display:swap}.warning{color:#ff0f0f}.inputWarning{border:2px solid #ff0f0f}div.meetings{padding-top:20px}div.meetings .title{text-align:center}div.meetings table{border:1px solid lightgray;border-collapse:collapse;width:90%;margin:auto;margin-top:20px}div.meetings table th,div.meetings table td{text-align:center;padding:5px}div.meetings table tbody tr:nth-child(odd){background-color:#f2f2f2}@media screen and (max-width: 1023px){div.meetings table{width:100%}}
